import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const Generic = (props) => (
    <div>
        <Helmet>
            <title>Fleet Management Applications | Mike Weisert | mikew.design</title>
            <meta name="description" content="Redefining the Fleet Management Experience" />
        </Helmet>

        <div id="main" className="alt">
            <section id="banner" className="major fleet">
                <div className="inner">
                    <div className="content">
                    <header className="major">
                        <h1>Redefining the Fleet Management Experience</h1>
                        <div className="row">
                            <div className="12u 12u$(small)">For the past 2 years, I've been working with the leading company in the Fleet Management space to transform the client experience using their online system.  This project involves a complete redesign of an ordering system which will process $2B+ of vehicle sales annually, as well as future visioning of key tools for clients to proactively manage their fleet.</div>
                            
                        </div><div className="empDates">2016-Present</div>
                    </header>
                            
                    </div>
                </div>
            </section>
            <section id="glance">
            <div className="inner">
                    <div className="content">
                    <h2>Overview</h2> 
                        <div className="row">
                        
                            <div className="self 8u 12u$(small)">
                                <h3>Achievements</h3>
                                <ul>
                                    <li>Helped define a shared style guide based on Atomic Design for the organization</li>
                                    <li>Created methodology for unmoderrated testing to ensure usability prior to team development</li>
                                    <li>Aligned team to key deliverables and vision by prototyping long term path</li>
                                    <li>Helped define the long term vision to leadership to secure ongoing funding for the project ($20m+)</li>
                                    <li>Presenter at organization's Account Management summit regarding client insights</li>
                                    <li>Regular presenter on future functionality to the steering council which includes the C-Suite.</li>
                                    <li>Directing multiple UX team members on multiple workstreams</li>
                                </ul>
                            </div>
                        
                            <div className="client 4u 12u$(small)">
                                <h3>The Client</h3>
                                <ul>
                                    <li>Leader in the Fleet Management space</li>
                                    <li>Processes $2B+ in vehicle sales annually</li>
                                    <li>Multi-year project to rethink 3 decades of business processes</li>
                                    <li>Agile team environment</li>
                                </ul>
                            </div>
                        </div>
                        </div>
                    </div>
            </section>
            
            <section id="three">
                <div className="inner">
                    <div className="content">
                        <h3>The Approach</h3>
                            <p>
                                When I joined the team, the client had attempted to complete the ordering redesign project multiple times in the past.  Where they had struggled was creating the end to end vision to work toward.  Too often the team had developed high-fidelity approaches and began development without considering how all the design elements would work together.
                                I began the project by researching both with clients and internal users.  We learned about existing pain points and opportunities to enhance the experience.  I translated these into user journeys which aligned the team toward our target audiences and their goals.
                                This client works in an Agile environment, which in the past resulted in 'just in time' designs that were often untested and not considerate of future functionality.  To break this cycle, we created an iterative design approach that broadly defined the product in a low-fidelity wireframe.  
                                We used our wireframe to begin testing early and often, both internally and with clients. The feedback we received allowed us to layer on detail as we approached the actual epics and sprints to develop the product.  I also focused on multiple facets of the product at a single time to avoid designing a solution that wasn't modular.
                            </p>
                        <h3>Atomic Design System</h3> 
                            <p>
                                Knowing we were building a modular designing system, we decided to employ an Atomic design approach for our design system.  I work closely with the lead UI designer for the client to properly define the design elements that reside in a single location for the company to reference.  We're currently working with Front-End developers to extend our design system to also include technical implementation details.
                            </p>
                        <h3>Advanced Prototyping &amp; Unmoderated Testing</h3>
                            <p>
                            As the project has progressed, we found user acceptance wasn't quite where we wanted it as we were releasing epics.  The problem involved our testing methodology, which was primarily conducted with a moderator.  When the user was asked to perform tests with a moderator, we found them to be far more likely to put thought into the task at hand.  This differs from how we actually use the web, generally on intuition and scanning visual indicators.  To solve this issue, I created a process for unmoderated testing using Axure, Google Tag Manager, Screen capture software and SurveyMonkey.  Details of this approach was given as part of a Meetup held at Rightpoint.
                                <ul>
                                    <li><a href="https://drive.google.com/file/d/1sEDJmeAv2hZ5hLnJhtsp5yibK7DqfZGZ/view?usp=sharing" target="_blank">Presentation Slides</a></li>
                                    <li><a href="https://drive.google.com/file/d/18sW_W83a9xomHCkPq4oD1kqqCtHMjvE9/view?usp=sharingDeck" target="_blank">Video</a></li>
                                </ul>
                            Using this method, we've been able to validate ideas or challenge a previously held notion in our design when the situation calls for it.
                            </p>
                        <h3>Designing the client experience for Fleet Management</h3>
                            <p>
                            Based on the successes from the ordering project, we've broadened the scope of our efforts.  Leadership tasked me help define 'proactive fleet management' where the software guides the client what they should be focused on to better manage their fleet.
                            This effort involves:
                            <ul>
                                <li>Client Reseaerch: Traveled to the organization's client sites across the US.  I conducted 4 hour sessions with clients to better learn how they are interacting with their fleet, and what opportunities we might incorporate into the software.</li>
                                <li>Based on findings, created product recommendations and directed other members of the UX team to create concepts; many of which are currently under development.</li>
                                <li>Product recommendations have been presented across the organization, including all levels of leadership to obtain buy in.</li>
                            </ul>



                            </p>
                    </div>
                </div>
            
            </section>

            <section id="four">
                <div className="inner">
                    <Link to="/ci" className="button big">Next: Children International</Link>
                </div>        
            </section>
        </div>

    </div>
)

export default Generic


